<template> 
       <div class="nav">
	        <div class="nav-top">
	            <div id="mini" style="border-bottom:1px solid rgba(255,255,255,.1)"><img src="../assets/images/mini.png" ></div>
	        </div>
	        <ul>
	            <li class="nav-item">
	                <a href="javascript:;"><i class="my-icon nav-icon icon_1"></i><span></span><i class="my-icon nav-more"></i></a>
	                <ul>
                      <li><router-link to="/main/admstudent"><span></span></router-link></li>
	                </ul>
	            </li>
	            <!-- <li class="nav-item">
	                <a href="javascript:;"><i class="my-icon nav-icon icon_2"></i><span></span><i class="my-icon nav-more"></i></a>
	                <ul>
	                    <li><router-link to="/main/admgroup"><span></span></router-link></li>
	                </ul>
	            </li> -->
	            <li class="nav-item">
	                <a href="javascript:;"><i class="my-icon nav-icon icon_3"></i><span></span><i class="my-icon nav-more"></i></a>
	                <ul>
	                    <li><router-link to="/main/uplcourse"><span></span></router-link></li>
	                    <li><router-link to="/main/admcourse"><span></span></router-link></li>
	                    <li><router-link to="/main/down"><span></span></router-link></li>
	                </ul>
	            </li>
	          </ul>
	      </div>
</template>
<script>
import "../assets/css/nav.css";
import "../assets/fonts/iconfont.css";
import "../assets/js/jquery-3.2.1.js"
import "../assets/js/jquery-1.11.3.js"

export default {
  data() {
    return {};
  },
  mounted() {
    (() => {
      $(".nav-item>a").on("click", function() {
        if (!$(".nav").hasClass("nav-mini")) {
          if (
            $(this)
              .next()
              .css("display") == "none"
          ) {
            //
            $(".nav-item")
              .children("ul")
              .slideUp(300);
            $(this)
              .next("ul")
              .slideDown(300);
            $(this)
              .parent("li")
              .addClass("nav-show")
              .siblings("li")
              .removeClass("nav-show");
          } else {
            //
            $(this)
              .next("ul")
              .slideUp(300);
            $(".nav-item.nav-show").removeClass("nav-show");
          }
        }
      });
      //nav-mini
      $("#mini").on("click", function() {
        if (!$(".nav").hasClass("nav-mini")) {
          $(".nav-item.nav-show").removeClass("nav-show");
          $(".nav-item")
            .children("ul")
            .removeAttr("style");
          $(".nav").addClass("nav-mini");
        } else {
          $(".nav").removeClass("nav-mini");
        }
	  });
    })();
  }
};
</script>
<style scoped>
	.nav-top img {
	position: relative;
	z-index: 99;
}
</style>