<template>
    <div class="disban-main">
        <div class="d-main">
           <div class="dmain-flex">
                <label for="stuClass"></label>
                <select @change="groupChange()" id="stuClass">
                    <option  v-for="(item, index) in cName" :key="index" :value="item.name">{{item.name}}</option>
                </select>
                <label for="stuGroup"></label>
                <select  id="stuGroup">
                    <option v-for="(item, index) in group" :data-gid="item.gid" :key="index" :value="item.name">{{item.name}}</option>
                </select>
                <button class="btn btn-info" @click.prevent="LoadGroup(1)" ></button>
                <button class="btn btn-info" @click.prevent="CheckGroupPwd()" data-toggle="modal" data-target="#seeGrouppwd"></button>
                <button class="btn btn-danger" data-toggle="modal" data-target="#deleGroup"></button>
           </div>
            <hr>
            <table class="table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-if="!stu">
                        <td></td>
                    </tr>
                    <tr v-else v-for="(item, i) in stu" :key="i">
                        <td>{{item.stuUid}}</td>
                        <td>{{item.stuName}}</td>
                        <td>{{claName}}</td>
                        <td>{{graName}}</td>
                        <td>{{item.stuLevel|lvTrans}}</td>
                        <td>
                            <button @click.prevent="changeId(item.stuUid)" class="btn btn-warning" data-toggle="modal" data-target="#kickStu"></button>&nbsp;
                        </td>
                    </tr>
                </tbody>
            </table>
            <div v-if="stu"  class="btn-group btnCount" role="group">
                <button v-if="pno-1>0" @click.prevent="LoadGroup(pno-1)" type="button" class="btn btn-info"></button>
                <button v-if="pno-2>0" @click.prevent="LoadGroup(pno-2)" type="button" class="btn btn-info">{{pno-2}}</button>
                <button v-if="pno-1>0" @click.prevent="LoadGroup(pno-1)" type="button" class="btn btn-info">{{pno-1}}</button>
                <button type="button"  @click.prevent="LoadGroup(pno)" class="btn btn-info active">{{pno}}</button>
                <button v-if="pno+1<=count" @click.prevent="LoadGroup(pno+1)" type="button" class="btn btn-info">{{pno+1}}</button>
                <button v-if="pno+2<=count" @click.prevent="LoadGroup(pno+2)" type="button" class="btn btn-info">{{pno+2}}</button>
                <button v-if="pno+1<=count" @click.prevent="LoadGroup(pno+1)" type="button" class="btn btn-info"></button>
            </div>
           <!--  -->
            <div v-if="stu" class="modal fade" id="kickStu" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3>"{{stu[index].stuUid}}""{{stu[index].stuName}}"</h3>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                            <button type="button" @click.prevent="kickOutGroup" class="btn btn-danger"></button>
                        </div>
                   </div>
               </div>
            </div>
            <!--  -->
            <div v-if="stu"  class="modal fade" id="deleGroup" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3></h3>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                            <button type="button" @click.prevent="deleGroup()" class="btn btn-danger"></button>
                        </div>
                   </div>
               </div>
            </div>
            <!--  -->
            <div v-if="stu" class="modal fade" id="seeGrouppwd" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <h3>{{gropName}}</h3>
                            <hr>
                            <p><input disabled="disabled" type="text" :value="gropkey"></p>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                        </div>
                   </div>
               </div>
            </div>
       
        </div>
    </div>
</template>
<script>
    import "../assets/css/AdminGroup.css"
    import "../assets/css/bootstrap.css"
    import "../apiFilter/myFilter.js"

    export default {
        data(){
            return{
                gropkey:null,
                gropName:null,
                graName:null,
                claName:null,
                group:null,
                cName:null,
                index:0,
                pageCount:10,//
                pno:null,//
                count:null,//
                stu:null
             }
        },
        methods:{
            changeId(i){
                this.index = i;
            },
            kickOutGroup(){
                let uid = this.index;
                let gid = $("#stuGroup option:selected").data("gid");
                let arr={
                    gid,
                    uid,
                    how:"kickOutGroup",
                     do:"UpdateStudent",
                }
                console.log(arr);
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    dataType: "json",
                    // async:false
                }).then(result=>{
                    // console.log(result);
                    if(result["talk"]=="Ok"){
                        alert("");
                        $('#kickStu').modal('hide');
                        window.location.reload();
                    }   
                    else{
                        alert("");
                    }
                })
            },
            deleGroup(){
                let gid = $("#stuGroup option:selected").data("gid");
                console.log(gid);
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                let arr={
                    gid,
                    how:"deleteGroup",
                    do:"UpdateStudent"
                }
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    dataType: "json",
                    // async:false
                }).then(result=>{
                    // console.log(result);
                    if(result["talk"]=="Ok"){
                        alert("");
                        $('#deleGroup').modal('hide');
                        window.location.reload();
                    }   
                    else{
                        alert("");
                    }
                })
            },
            CheckGroupPwd(){
                let gid = $("#stuGroup option:selected").data("gid");
                console.log(gid);
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                let arr={
                    gid,
                    do:"ShowGroupKey"
                }
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    dataType: "json",
                    // async:false
                }).then(result=>{
                    console.log(result);
                    this.gropkey = result["group"]["groupkey"];
                    console.log(this.gropkey)
                    alert(""+this.gropkey)
                    this.gropName = result["group"]["name"];
                    console.log(this.gropName)

                })
            },
            groupChange(){
                let claName =""
                if($("#stuClass option:selected").length>0){
                    claName = $("#stuClass option:selected")[0].value;
                }
                let cid = sessionStorage.getItem('cid');
                let uid = sessionStorage.getItem('uid');
                let level = sessionStorage.getItem('lve');
                let pageCount = this.pageCount;      
                let arr = {
                    showGroup:"Y",
                    updateGroup:"Y",
                    claName,
                    uid,
                    level,
                    cid,
                    pageCount,
                    do:"ShowStudent"
                }
                console.log("")
                console.log(arr);
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    dataType: "json",
                    // async:false
                }).then((result) => {
                    console.log("")
                    console.log(result);
                    if(result["talk"]=="NotGroup"){
                        alert("");
                          this.group = "";
                    }
                    else{
                        this.group = result["group"];
                        // console.log(this.count);
                    }
                });
            },
            LoadGroup(pno){
                let claName =""
                 if($("#stuClass option:selected").length>0){
                    claName = $("#stuClass option:selected")[0].value;
                }
                let stuGroup =""
                if($("#stuGroup option:selected").length>0){
                    stuGroup = $("#stuGroup option:selected")[0].value;
                }
                let cid = sessionStorage.getItem('cid');
                let uid = sessionStorage.getItem('uid');
                let level = sessionStorage.getItem('lve');
                let pageCount = this.pageCount;      
                let arr = {
                    showGroup:"Y",
                    claName,
                    stuGroup,
                    uid,
                    level,
                    cid,
                    pageCount,
                    pno,
                    do:"ShowStudent"
                }
                console.log("")
                console.log(arr);
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    dataType: "json",
                    async:false
                }).then((result) => {
                    console.log("")
                    console.log(result);
                    if(result["talk"]=="NotGroup"){
                        alert("");
                    }
                    else{
                        this.stu = result["student"];
                        this.count = Math.ceil(result["howNum"]);
                        console.log(this.count);
                        this.pno = parseInt(result["pno"]);
                        console.log(this.pno);
                        this.cName = result["class"];
                        this.group = result["group"];
                        this.claName = result["classOne"];
                        this.graName = result["groupOne"];
                        // console.log(this.count);
                    }
                }); 
            }
        },
        beforeMount() {
            this.LoadGroup(1); 
        }     
    }
</script>