<template>
    <div class="admvideo-main">
        <div class="video-search">
            <span></span>
            <select name="v-claName">
                <option v-for="(item, index) in claName" :key="index" :value="item">{{item}}</option>
            </select>
             <span></span>
            <select name="v-claCourse">
                <option v-for="(item, index) in course" :key="index" :value="item.couid">{{item.title}}</option>
            </select>
            <button @click.prevent="LoginCourse" class="btn btn-info"></button>
        </div>
        <span class="result-font">
            <p></p>
        </span>
        <div class="video-result">
            <table class="table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-if="!oneCourse">
                        <td></td>
                    </tr>
                    <tr v-else>    
                        <td style="width:127px">{{claName.name}}</td>
                        <td style="width:305px">{{oneCourse.title}}</td>
                        <td style="width:120px">{{oneCourse.date|dateTrans}}</td>
                        <td style="width:462px; max-width: 300px;overflow: hidden;text-overflow: ellipsis;white-space: nowrap">{{oneCourse.video_src}}</td>
                        <td style="width:111px">{{oneCourse.video_kind|videoTrans}}</td>
                        <td style="width:146px">{{oneCourse.task_src|fileTrans}}</td>
                        <td style="width:292px" class="btnModal">
                            <button @click.prevent="DeleCourse(oneCourse.couid)" class="btn btn-danger"></button>&nbsp;
                            <button @click.prevent="DeleFile(oneCourse.couid)" v-if="cour" class="btn btn-danger" ></button>
                            <button v-else  class="btn btn-success" data-toggle="modal" data-target="#UploadFile" ></button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        <!--  -->
        <div class="modal fade" id="UploadFile" tabindex="-1" role="dialog" aria-labelledby="myModalLabel">
               <div class="modal-dialog" role="document">
                   <div class="modal-content">
                       <div class="modal-header">
                         <h3 class="modal-title" id="myModalLabel"></h3>
                        </div>
                        <div class="modal-body">
                            <p>2M</p>
                            
                                <div class="row">
                                    <a href="javascript:;" class="ui-upload">
                                        <input type="file" name="fileToUpload" id="fileToUpload" @change="fileSelected()"/>
                                    </a>
                                </div>
                                    <div id="fileName"></div>
                                    <div id="fileSize"></div>
                                    <div id="fileType"></div>
                        </div>
                        <div class="modal-footer">
                            <button type="button" class="btn btn-default" data-dismiss="modal"></button>
                            <button @click="uploadFile(oneCourse.couid)" class="btn btn-success"></button>
                        </div>
                   </div>
            </div>
        </div>
    </div>
</template>
<script>
    import "../assets/css/bootstrap.css"
    import "../assets/css/AdminCourse.css"
    import "../apiFilter/myFilter.js"

    export default {
        data(){
            return{
                claName:null,
                oneCourse:null,
                course:null,
                cour:null,
                NotWork:false
            }
        },
        methods:{
            LoginCourse(){
                let couid = "";
                if($("select[name='v-claCourse'] option:selected").length>0){
                    couid = $("select[name='v-claCourse'] option:selected")[0].value;
                }
                let cid = sessionStorage.getItem('cid');
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                let arr = {
                    couid,
                    cid,
                    do:"FindCou"
                }
                console.log(arr);
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    async:false,
                    dataType: "json"
                }).then((result) => {
                    console.log(result);
                    if(result['error']==""){
                        this.NotWork = true;
                    }
                    else{
                        this.claName = result['class'];
                        this.oneCourse = result['oneCourse'];
                        this.course = result['course'];
                        if(this.oneCourse.task_src==0){
                            this.cour = false;
                        }
                        else{
                            this.cour = true;
                        }
                    }
                })
            },
            DeleCourse(value){
                let couid = value;
                let arr ={
                    couid,
                    do:"DeleteCou"
                }
                console.log(arr);
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    dataType: "json",
                }).then(result=>{
                    console.log(result);
                    if(result["talk"]=="Ok"){
                        alert("");
                        window.location.reload();
                    }
                    else{
                        alert("");
                    }
                })
            },
            DeleFile(value){
                let couid = value;
                let arr ={
                    couid,
                    do:"DeleteWork"
                }
                console.log(arr);
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    dataType: "json",
                }).then(result=>{
                    console.log(result);
                    if(result["talk"]=="Ok"){
                        alert("");
                        window.location.reload();
                    }
                    else{
                        alert("");
                    }
                })
            },
            fileSelected() {
                var file = document.getElementById('fileToUpload').files[0];
                if((Math.round(file.size * 100 / (1024 * 1024)) / 100).toString()>2){
                    alert("2M,");
                    return
                }      
                console.log(file.size)
                if (file) {
                    var fileSize = 0;
                    if (file.size > 1024 * 1024)
                    fileSize = (Math.round(file.size * 100 / (1024 * 1024)) / 100).toString() + 'MB';
                    else
                    fileSize = (Math.round(file.size * 100 / 1024) / 100).toString() + 'KB';
                    document.getElementById('fileName').innerHTML = ': ' + file.name;
                    document.getElementById('fileSize').innerHTML = ': ' + fileSize;
                }
            },
            uploadFile(value) {
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                // let form = document.getElementById('form1');
                let cid = sessionStorage.getItem('cid');
                let couid = value;
                let file = document.getElementById("fileToUpload").files[0];
                let form = new FormData();
                form.append('cid',cid)
                form.append('couid',couid)
                form.append('file',file)
                form.append('do',"InWork")
                console.log(form);
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: form,
                    processData:false,
                    contentType: false,
                    dataType: "json",
                }).then(result=>{
                    console.log(result);
                    if(result["talk"]=="Ok"){
                        alert("")
                        window.location.reload();
                    }
                    else{
                        alert("")
                    }
                })
            },
        },
        beforeMount() {
            this.LoginCourse();  
        } 
     
    }
</script>
<style>
    a:hover{
        text-decoration: none;
    }

    .ui-upload {
      font-size: 14px;
      width: 80px;
      height: 30px;
      line-height: 30px;
      text-align: center;
      position: relative;
      cursor: pointer;
      color: #fff;
      background: #00abff;
      border-radius: 3px;
      overflow: hidden;
      display: inline-block;
      text-decoration: none;
      left: 15px;
    }
    .ui-upload input {
      position: absolute;
      font-size: 100px;
      right: 0;
      top: 0;
      opacity: 0;
      filter: alpha(opacity=0);
      cursor: pointer
    }
</style>
