<template>
    <div class="downwork-main">
        <div class="down-flex">
             <select name="" id="">
                <option v-if="NotWork"></option>
                <option v-else>{{claName.name}}</option>
            </select>
            <select name="d-Course">
                <option v-for="(item, index) in course" :key="index" :value="item.couid">{{item.title}}</option>
            </select>
            <button @click.prevent="LoginWork(1)" class="btn btn-info"></button>
            <button @click.prevent="downAllWork()" class="btn btn-info"></button>
        </div>
         <span class="result-font">
            <p></p>
            <div class="btn-group" role="group">
                <button v-if="pno-1>0" type="button" class="btn btn-info"></button>
                <button v-if="pno-2>0" type="button" class="btn btn-info">{{pno-2}}</button>
                <button v-if="pno-1>0" type="button" class="btn btn-info">{{pno-1}}</button>
                <button type="button" class="btn btn-info active">{{pno}}</button>
                <button v-if="pno+1<=pageCount" type="button" class="btn btn-info">{{pno+1}}</button>
                <button v-if="pno+2<=pageCount" type="button" class="btn btn-info">{{pno+2}}</button>
                <button v-if="pno+1<=pageCount" type="button" class="btn btn-info"></button>
            </div>
        </span>
        <div class="down-table">
            <table class="table">
                <thead>
                    <tr>
                        <th></th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-if="NotWork" >
                        <td></td>
                    </tr>
                    <tr v-else v-for="(item, index) in work" :key="index">
                        <td>{{item.task_id}}</td>
                        <td>{{item.name}}</td>
                        <td>{{courName.title}}</td>
                        <td><button class="btn btn-success" @click.prevent="downWork(item.task_id)"></button></td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>
<script>
    import "../assets/css/DwonWork.css"
    import axios from 'axios'

    export default {
        data(){
            return{
                course:null,
                claName:null,
                index:0,
                pageCount:10,
                pno:null,
                work:null,
                courName:null,
                urlwork:null,
                zipname:null,
                NotWork:false
            }
        },
        methods:{
            downWork(value){
                let task_id = value;
                let ip = "http://localhost";
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                let arr = {
                     task_id,
                     do:"DwWork",
                     how:"one"
                }
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    async:false,
                    dataType: "json"
                }).then((result) => {
                    console.log(result);
                    this.zipname = result.zipname
                })
                window.open(ip+this.urlwork+this.zipname);

            },
            LoginWork(pno){
                let couid = "";
                if($("select[name='d-Course'] option:selected").length>0){
                    couid = $("select[name='d-Course'] option:selected")[0].value;
                }
                let cid = sessionStorage.getItem('cid');
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                let pageCount = this.pageCount;
                let arr = {
                    pageCount,
                    pno,
                    couid,
                    cid,
                    do:"ShowWork"
                }
                console.log(arr);
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    async:false,
                    dataType: "json"
                }).then((result) => {
                    console.log(result);
                    if(result['talk']=="NotOk"){
                        this.NotWork = true;
                    }
                    else{
                        this.course = result['course'];
                        this.claName = result['class'];
                        this.pno = result['pno'];
                        this.work = result['work'];
                        this.courName =result['claName'];
                        this.urlwork = result['url'];
                    }
                   
                })
            },
            downAllWork(){
                let ip = "http://localhost";
                let url = "http://localhost/SchoolOnline/";
                let urlback = "Php/admin.php";
                let couid ;
                if($("select[name='d-Course'] option:selected").length>0){
                    couid = $("select[name='d-Course'] option:selected")[0].value;
                }
                console.log(couid)
                let arr = {
                     couid,
                     do:"DwWork",
                     how:"all"
                }
                $.ajax({
                    type: "post",
                    url: url+urlback,
                    data: arr,
                    async:false,
                    dataType: "json"
                }).then((result) => {
                    console.log(result);
                    this.zipname = result.zipname
                })
                window.open(ip+this.urlwork+this.zipname);
                console.log(ip+this.urlwork+this.zipname)
                
            }
        },
        beforeMount() {
            this.LoginWork(1);  
        } 
    }
</script>