import Vue from 'vue'
import Router from 'vue-router'
import Login from "./views/Login.vue"
import Main from "./views/Main.vue"
import AdminStudent from "./views/AdminStudent.vue"
import AdminGroup from "./views/AdminGroup.vue"
import UploadCourse from "./views/UploadCourse.vue"
import AdminCourse from "./views/AdminCourse.vue"
import DownWork from "./views/DownWork.vue"

Vue.use(Router)
const router = new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
    },
    {
      path: '/login',
      name: 'login',
      component: Login
    },
    {
      path:'/main',
      name:'main',
      component: Main,
      children:[
        {
          path:'',
          redirect:{name:'admstudent'}
        },
        {
          path:'admstudent',
          name:'admstudent',
          component:AdminStudent
        },
        {
          path:'admgroup',
          name:'admgroup',
          component:AdminGroup,
        },
        {
          path:'uplcourse',
          name:'uplcourse',
          component:UploadCourse,
        },
        {
          path:'admcourse',
          name:'admcourse',
          component:AdminCourse,
        },
        {
          path:'down',
          name:'down',
          component:DownWork,
        },

      ]
    },
    {
      path: '**',
      redirect: '/login',
    }
  ]
})

router.beforeEach((to,from,next)=>{
  console.log('..'); 
  const nextRoute = ['main','admstudent','admgroup','admclass','uplcourse','admcourse','creclass','down'];//
  if(nextRoute.indexOf(to.name) >= 0) { //nextRoute
    if ((sessionStorage.getItem('uid'))==null) { //
      console.log('');
      next('/login')   //           
    }
    else{
      console.log("")
      next();
    }
  }
  else{
    next();
  }
});

export default router;